import Router from '@/router.js'
import Store from '@/store/index.js'

const jumper = {
  toHome () {
    Router.replace({ name: 'home' })
  },
  toScan (figure) {
    Router.replace({ name: 'scan', params: { id: Store.getters.isStaff ? figure.token : figure.studentId } })
  }
}

export default jumper
