const base_url = mock.base_url

let eventCb = null

let bluetooth = 'false'

let mac = null

function delay (cb, result, time = 2000) {
  setTimeout(function () {
    cb(result)
  }, time)
}

function emit (type, data) {
  if (window[eventCb]) {
    window[eventCb](JSON.stringify({
      type,
      data
    }))
  }
}

function callHandler (funcName, data, cb) {
  switch (funcName) {
    case 'getNativeOS':
      cb('android')
      break
    case 'isBluetoothOpen':
      cb(bluetooth)
      break
    case 'openSysSetting':
      cb(bluetooth = 'true')
      emit(102)
      break
    case 'scanQRCode':
      delay(cb, JSON.stringify(Math.random() > 0.5 ? {
        type: 0
      } : {
        type: 1,
        message: `G1MW99301825C#${ mock.random.string('upper', 12) }#42C89500`
      }))
      break
    case 'ringUp':
      console.log(`will call ${ data.phoneNumber }`)
      break
    case 'isBraceletConnected':
      delay(cb, JSON.stringify(mac ? {
        type: true,
        mac
      } : {
        type: false
      }))
      break
    case 'disConnectBracelet':
      mac = null
      delay(cb, 'true')
      break
    case 'connectBracelet':
      mac = data.mac
      delay(cb, 'true')
      break
    default:
      break
  }
}

setTimeout(function () {
  if (!window.JsNativeApi.isReady && window.WVJBCallbacks) {
    window.WVJBCallbacks[0]({
      registerHandler: function (callback) {
        eventCb = callback
      },
      callHandler
    })
  }
}, 5000)

export default {
  timeout: 15000,
  log,
  base_url // 为'/'时表示通过vue.config.js设置代理
}

let logElement = null

export function log (str) {
  if (!logElement) {
    logElement = document.createElement('div')
    logElement.style = 'position: absolute; left: 0; top: 0; bottom: 0; width: 100px; overflow: auto; font-size: 12px; color: #f00;'
    document.body.appendChild(logElement)
  }
  let element = document.createElement('div')
  element.innerText = str
  logElement.appendChild(element)
}
