import Vue from 'vue'
import VueRouter from 'vue-router'
import Store from '@/store/index.js'

Vue.use(VueRouter)

// 登录相关
import Login from '@/views/login/Login.vue'
// import PreSetPwd from '@/views/login/PreSetPwd.vue'
import ResetPwd from '@/views/login/ResetPwd.vue'
// 添加主题的wrapper
import ThemeWrapper from '@/views/wrapper/ThemeWrapper.vue'
// 手环相关
import BindChoosen from '@/views/bracelet/BindChoosen.vue'
import BindSuccess from '@/views/bracelet/BindSuccess.vue'
import BindFailure from '@/views/bracelet/BindFailure.vue'
import Scan from '@/views/bracelet/Scan.vue'
import Unbind from '@/views/bracelet/Unbind.vue'
import Bracelet from '@/views/bracelet/Bracelet.vue'
// 主页
import Home from '@/views/shared/Home.vue'
// 打卡记录
import History from '@/views/shared/SignInHistory.vue'
// 打卡运动
import Motion from '@/views/shared/Motion.vue'
// 用户账号
import AccountInfo from '@/views/shared/AccountInfo.vue'
// 蓝牙开启
import Bluetooth from '@/views/shared/Bluetooth.vue'
// 侧边栏相关
import Setting from '@/views/side-menu/Setting.vue'
import ModifyEmail from '@/views/side-menu/ModifyEmail.vue'
import ModifyLanguage from '@/views/side-menu/ModifyLanguage.vue'
// 孩子相关
import Children from '@/views/parent/Children.vue'
import ChildrenInfo from '@/views/parent/ChildrenInfo.vue'
import ModifyBus from '@/views/parent/ModifyBus.vue'
// 修改手机相关
import ModifyMobileWrapper from '@/views/modify-mobile/ModifyMobileWrapper.vue'
import ModifyMobileConfirm from '@/views/modify-mobile/ModifyMobileConfirm.vue'
import ModifyMobileValidate from '@/views/modify-mobile/ModifyMobileValidate.vue'
import ModifyMobile from '@/views/modify-mobile/ModifyMobile.vue'
// 修改密码相关
import ModifyPwdWrapper from '@/views/modify-pwd/ModifyPwdWrapper.vue'
import ModifyPwdPresent from '@/views/modify-pwd/ModifyPwdPresent.vue'
import ModifyPwd from '@/views/modify-pwd/ModifyPwd.vue'
// 考勤相关
import AttendanceWrapper from '@/views/attendance/AttendanceWrapper.vue'
import AttendanceSchool from '@/views/attendance/AttendanceSchool.vue'
import AttendanceGrade from '@/views/attendance/AttendanceGrade.vue'
import AttendanceClasses from '@/views/attendance/AttendanceClasses.vue'
import AttendanceClass from '@/views/attendance/AttendanceClass.vue'
import SignInInformation from '@/views/attendance/SignInInformation.vue'
import AttendanceHistory from '@/views/attendance/AttendanceHistory.vue'
// Google Map
import BusMap from '@/views/shared/BusMap.vue'

const router = new VueRouter({
  routes: [
    {
      path: '/login/:mode(parent|staff)?',
      name: 'login',
      component: Login,
      meta: {
        type: 'login'
      }
    },
    // {
    //   path: '/forget/:mode(parent|staff)/pwd',
    //   name: 'pre-reset-pwd',
    //   component: PreSetPwd,
    //   meta: {
    //     type: 'login'
    //   }
    // },
    // {
    //   path: '/forget/:mode(parent|staff)/pwd/:token',
    //   name: 'reset-pwd',
    //   component: ResetPwd,
    //   meta: {
    //     type: 'login'
    //   }
    // },
    {
      path: '/user',
      component: ThemeWrapper,
      children: [
        {
          path: 'boot/pwd/:token?',
          name: 'set-pwd',
          component: ResetPwd,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'bind/:type?',
          name: 'bind',
          component: BindChoosen,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'scan/:id',
          name: 'scan',
          component: Scan,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'bind/success/:id',
          name: 'bind-success',
          component: BindSuccess,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'bind/failure/:id',
          name: 'bind-failure',
          component: BindFailure,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'unbind/:id',
          name: 'unbind',
          component: Unbind,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'home/:index(\\d+)?',
          name: 'home',
          component: Home,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'history/:id',
          name: 'history',
          component: History,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'motion/:id',
          name: 'motion',
          component: Motion,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'bracelet',
          name: 'bracelet',
          component: Bracelet,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'account',
          name: 'account',
          component: AccountInfo,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'pwd',
          component: ModifyPwdWrapper,
          children: [
            {
              path: 'preset',
              name: 'pre-modify-pwd',
              component: ModifyPwdPresent,
              meta: {
                login: true,
                mode: true
              }
            },
            {
              path: 'modify',
              name: 'modify-pwd',
              component: ModifyPwd,
              meta: {
                login: true,
                mode: true
              }
            }
          ],
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'email/modify',
          name: 'modify-email',
          component: ModifyEmail,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'mobile',
          component: ModifyMobileWrapper,
          children: [
            {
              path: 'confirm',
              name: 'modify-mobile-confirm',
              component: ModifyMobileConfirm,
              meta: {
                login: true,
                mode: true
              }
            },
            {
              path: 'validate',
              name: 'pre-modify-mobile',
              component: ModifyMobileValidate,
              meta: {
                login: true,
                mode: true
              }
            },
            {
              path: 'modify',
              name: 'modify-mobile',
              component: ModifyMobile,
              meta: {
                login: true,
                mode: true
              }
            }
          ],
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'bluetooth',
          name: 'bluetooth',
          component: Bluetooth,
          meta: {
            login: true,
            mode: true
          }
        },
        {
          path: 'setting',
          name: 'setting',
          component: Setting,
          meta: {
            login: true
          }
        },
        {
          path: 'setting/language',
          name: 'modify-language',
          component: ModifyLanguage,
          meta: {
            login: true
          }
        }
      ]
    },
    {
      path: '/children',
      name: 'children',
      component: Children,
      meta: {
        login: true,
        mode: 'parent'
      }
    },
    {
      path: '/children/:id',
      name: 'children-info',
      component: ChildrenInfo,
      meta: {
        login: true,
        mode: 'parent'
      }
    },
    {
      path: '/children/:id/bus',
      name: 'modify-bus',
      component: ModifyBus,
      meta: {
        login: true,
        mode: 'parent'
      }
    },
    {
      path: '/attendance',
      component: AttendanceWrapper,
      children: [
        {
          path: ':type(teacher|student)/school/:date?',
          name: 'attendance-school',
          component: AttendanceSchool,
          meta: {
            login: true,
            mode: 'staff'
          }
        },
        {
          path: 'student/grade/:schoolLevelId/:date?',
          name: 'attendance-grade',
          component: AttendanceGrade,
          meta: {
            login: true,
            mode: 'staff'
          }
        },
        {
          path: 'student/classes/:gradeId/:date?',
          name: 'attendance-classes',
          component: AttendanceClasses,
          meta: {
            login: true,
            mode: 'staff'
          }
        },
        {
          path: 'student/class/:classId/:date?',
          name: 'attendance-class',
          component: AttendanceClass,
          meta: {
            login: true,
            mode: 'staff'
          }
        }
      ],
      meta: {
        login: true,
        mode: 'staff'
      }
    },
    {
      path: '/singin/student/:studentId',
      name: 'sing-in-student',
      component: SignInInformation,
      meta: {
        login: true
      }
    },
    {
      path: '/attendance/history/:studentId/:type(normal|abnormal)',
      name: 'attendance-history',
      component: AttendanceHistory,
      meta: {
        login: true
      }
    },
    {
      path: '/map/bus/:busId?/:date?',
      name: 'bus-map',
      component: BusMap,
      meta: {
        login: true
      }
    },
    {
      path: '*',
      redirect: '/login'
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (Store.getters.isLogined) {
    if (to.meta.type === 'login') {
      next({ name: 'home', params: { mode: Store.state.user.mode }, replace: true })
    } else {
      let mode = Store.state.user.mode
      // 校验用户模式
      // 当页面需区分用户模式时，true表示任意模式
      if (to.meta.mode && to.meta.mode !== mode) {
        if (to.meta.mode === true) {
          if (to.params.mode && to.params.mode !== mode) {
            next({ name: to.name, params: Object.assign({}, to.params, { mode }), replace: true })
          } else {
            next()
          }
        } else {
          next({ name: 'home', params: { mode: Store.state.user.mode }, replace: true })
        }
      } else {
        next()
      }
    }
  } else {
    if (to.meta.login) {
      next({ name: 'login', replace: true })
    } else {
      next()
    }
  }
})

export default router
