<template>
  <navigation-wrapper class="c-modify" :title="$t('account.email')">
    <div class="l-navigation__content l-form">
      <form-field :label="$t('email.label')" :error="emailErr" v-model.trim="email">
      </form-field>
      <form-field :label="$t('common.confirm')" :error="confirmErr" v-model="confirmEmail">
    </form-field>
      <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
      <div class="c-modify__error">{{ error }}</div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import navigationWrapper from '@/views/wrapper/NavigationWrapper.vue'
import formField from '@/components/FormField.vue'

export default {
  name: 'modify-email',
  components: {
    navigationWrapper,
    formField
  },
  data () {
    return {
      email: '',
      emailErr: '',
      confirmEmail: '',
      confirmErr: '',
      error: ''
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/email/modify' : 'parent/email/modify'
    },
    isValid () {
      return !!this.email && !!this.confirmEmail
    }
  },
  methods: {
    save () {
      if (this.isValid) {
        this.error = ''
        if (!/^[a-z0-9A-Z]+[-|a-z0-9A-Z._]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\.)+[a-z]{2,}$/.test(this.email)) {
          this.emailErr = this.$t('email.formatErr')
          this.confirmErr = ''
          return
        } else {
          this.emailErr = ''
        }
        if (this.confirmEmail !== this.email) {
          this.confirmErr = this.$t('email.notMatch')
          return
        } else {
          this.confirmErr = ''
        }
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: (res, data) => {
              this.user.email = data.email
              this.$router.back()
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          email: this.email
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

