<template>
  <div class="c-attendance-class">
    <percent-circle class="c-attendance-clas__percent" :result="result" :tip="$t('attendance.arrived')" :description="description">
    </percent-circle>
    <div class="c-attendance-class__students">
      <div v-for="(student, index) in students" :key="index" class="c-attendance-student" @click="viewDetails(student)" :class="{ 'is-nonarrival': student.status === 1, 'is-leave': student.status === 4, 'is-manual': student.status === 3, 'is-recorded': student.status === 2 }">
        <div class="c-attendance-student__avatar">
          <div class="c-solid-avatar">
            <div class="c-solid-avatar__icon" :style="{ backgroundImage: `url(${ student.headUrl }` }"></div>
          </div>
        </div>
        <div class="c-attendance-student__name">{{ student.lastName }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { formatToYYYYMMDD } from '@/utils/format.js'
import percentCircle from '@/components/PercentCircle.vue'

export default {
  name: 'attendance-class',
  props: ['date'],
  components: {
    percentCircle
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0,
        percent: 0
      },
      description: '',
      students: []
    }
  },
computed: {
  ...mapState(['user']),
    ...mapGetters(['isAdmin', 'isHeadTeacher']),
    classId () {
      return Number(this.$route.params.classId)
    },
    url () {
      return this.isAdmin ? 'admin/class/studentattendance' : 'teacher/class/studentattendance'
    }
  },
  methods: {
    analysis (data) {
      this.result = {
        arrived: data.arrivedStudentClassTotal,
        total: data.studentClassTotal,
        percent: data.studentClassTotal > 0 ? data.arrivedStudentClassTotal / data.studentClassTotal : 0
      }
      this.description = this.$t('format.classInfo', {
        gradeLevel: data.gradeLevel,
        classId: data.classId
      })
      this.students = data.studentAttendanceInfo
    },
    viewDetails (student) {
      this.$router.push({ name: 'sing-in-student', params: { studentId: student.idStudent, type: ''  } })
    },
    fetch () {
      if (this.isAdmin || this.isHeadTeacher) {
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              cycle: 60000
            },
            success: res => this.analysis(res.data)
          })
        }
        this.$httpPost.force(this.isAdmin ? {
          token: this.user.token,
          idClass: this.classId,
          queryDate: this.date
        } : {
          token: this.user.token,
          queryDate: this.date
        }, {
          cycle: formatToYYYYMMDD(new Date()) === this.date ? 60000 : 0
        })
      }
    },
    reset () {
      this.result = {
        arrived: 0,
        total: 0,
        percent: 0
      }
      this.students = []
      this.fetch()
    }
  },
  watch: {
    date () {
      this.reset()
    }
  },
  created () {
    this.fetch()
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-attendance-class {
  display: flex;
  flex-direction: column;
  font-size: 0;
  &__percent {
    flex: none;
    padding-top: 24px;
  }
  &__students {
    flex: auto;
    overflow-x: hidden;
  }
}
%student-status {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
}
.c-attendance-student {
  position: relative;
  display: inline-block;
  padding: 14px 5px;
  color: #000;
  text-align: center;
  &__avatar {
    position: relative;
    display: inline-block;
    line-height: 0;
  }
  &__name {
    width: 80px;
    padding: 8px 0;
    font-size: 12px;
    line-height: 1;
    white-space: nowrap;
    text-align: center;
    overflow: hidden;
  }
  &.is-nonarrival {
    opacity: 0.6;
  }
  &.is-manual {
    .c-attendance-student__avatar::after {
      @extend %student-status;
      @include bg-img-ratio('../../assets/attendance_manual', 39, 39);
    }
  }
  &.is-leave {
    .c-solid-avatar, .c-attendance-student__name {
      opacity: 0.6;
    }
    .c-attendance-student__avatar::after {
      @extend %student-status;
      @include bg-img-ratio('../../assets/attendance_leave', 39, 39);
    }
  }
  &.is-recorded {
    .c-attendance-student__avatar::after {
      @extend %student-status;
      @include bg-img-ratio('../../assets/attendance_normal', 39, 39);
    }
  }
}
</style>
