<template>
  <div id="app">
    <router-view />
    <div v-if="showMask" class="c-requesting-mask">
      <div class="c-requesting-mask__wrapper">
        <img width="30" height="30" src="./components/loading.gif" />
        <div class="c-requesting-mask__label">{{ $t('loading') }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import * as Event from '@/code/custom-event.js'

export default {
  name: 'app',
  data () {
    return {
      showMask: false,
      maskId: -1
    }
  },
  created () {
    this.$eventBus.$on(Event.REQUESTING, id => {
      this.showMask = true
      this.maskId = id
    })
    this.$eventBus.$on(Event.REQUEST_FINISHED, id => {
      if (this.maskId === id) {
        this.maskId = -1
        this.showMask = false
      }
    })
  },
  beforeMount () {
    let element = document.getElementById('loading')
    if (element) {
      document.body.removeChild(element)
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: Verdana, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background: #fff;
}

.c-requesting-mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 220px;
  text-align: center;
  z-index: 999;
  &__wrapper {
    display: inline-block;
    padding-top: 35px;
    width: 120px;
    height: 85px;
    text-align: center;
    border-radius: 5px;
    background-color: rgba(#000, 0.7);
  }
  &__label {
    margin-top: 10px;
    color: #fff;
    font-size: 14px;
  }
}
</style>
