<template>
  <div class="l-form c-modify">
    <!-- <form-field type="number" :placeholder="$t('mobile.newPhoneNumber')" v-model.trim="mobile">
      <area-code @change="pick"></area-code>
    </form-field> -->
    <form-field type="number" :label="$t('mobile.newPhoneNumber')" v-model.trim="mobile">
    </form-field>
    <form-field type="number" :label="$t('mobile.confirm')" :error="confirmErr" v-model.trim="confirmMobile">
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
    <div class="c-modify__error">{{ error }}</div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'

export default {
  name: 'modify-mobile',
  components: {
    formField,
    areaCode
  },
  data () {
    return {
      mobile: '',
      areaCode: '',
      confirmMobile: '',
      confirmErr: '',
      error: ''
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/mobile/modify' : 'parent/mobile/modify'
    },
    isValid () {
      return this.mobile && this.confirmMobile
    }
  },
  methods: {
    pick (areaCode) {
      this.areaCode = areaCode
    },
    save () {
      if (this.isValid) {
        this.error = ''
        if (this.confirmMobile !== this.mobile) {
          this.confirmErr = ''
        } else {
          this.confirmErr = ''
        }
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: (res, data) => {
              this.user.mobile = data.mobile
              this.$router.back()
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          mobile: this.mobile
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

