import '@/common/style/index.scss'
import 'swiper/dist/css/swiper.min.css'

import Vue from 'vue'
import App from '@/App.vue'
import store, { initialize } from '@/store/index.js'
import router from '@/router.js'
import i18n from '@/locale/index.js'
import inject from '@/inject'

const isDebug = process.env.NODE_ENV !== 'production'

Vue.config.productionTip = isDebug
Vue.config.debug = isDebug
Vue.config.devtools = isDebug

Vue.directive('overflow', {
  inserted: function (el) {
    if (el.offsetWidth <= el.parentNode.offsetWidth) {
      el.classList.remove('is-overflow')
    } else {
      el.classList.add('is-overflow')
    }
  },
  componentUpdated: function (el) {
    if (el.offsetWidth <= el.parentNode.offsetWidth) {
      el.classList.remove('is-overflow')
    } else {
      el.classList.add('is-overflow')
    }
  }
})

Promise.all([inject(), initialize()]).then(() => {
  new Vue({
    store,
    router,
    i18n,
    render: h => h(App)
  }).$mount('#app')
})
