<template>
  <div class="l-operation p-scan">
    <div class="l-operation__title">{{ $t('scan.title') }}</div>
    <div class="l-operation__header">{{ $t('scan.tip') }}</div>
    <div class="l-operation__content">
      <div class="p-scan__img"></div>
    </div>
    <div class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button t-adapt-button" @click="toScan">{{ $t('scan.scan') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'scan',
  computed: {
    ...mapState(['figures', 'runtimmeMode']),
    ...mapGetters(['isStaff', 'getFigure']),
    url () {
      return this.isStaff ? 'staff/bracelet/bind' : 'parent/bracelet/bind'
    },
    id () {
      return this.$route.params.id
    },
    figure () {
      return this.getFigure(this.id)
    }
  },
  methods: {
    cancel () {
      switch (this.runtimmeMode) {
        case 'boot':
          if (this.figures.length === 1) {
            this.$jumper.toHome()
          } else {
            this.$router.replace({ name: 'bind' })
          }
          break
        default:
          this.$router.back()
          break
      }
    },
    bind (serialNum, macAddress, nfcId) {
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            if (this.figure) {
              this.figure.braceletOfBinding = 1
              this.figure.mac = macAddress
            }
            this.$router.replace({ name: 'bind-success', params: { id: this.id } })
          },
          fail: () => {
            this.$router.replace({ name: 'bind-failure', params: { id: this.id } })
          }
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.id,
        serialNum,
        macAddress,
        nfcId
      } : {
        idStudent: this.id,
        serialNum,
        macAddress,
        nfcId
      })
    },
    toScan () {
      this.$native.scan().then(result => {
        if (result.type === 1) {
          try {
            let data = result.message.split('#')
            if (data.length === 3) {
              this.bind(data[0], data[1], data[2])
              return
            }
          } catch (e) { }
        }
        this.$router.replace({ name: 'bind-failure', params: { id: this.id } })
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss" scoped>
.p-scan {
  .l-operation__header {
    color: #333333;
    font-size: 14px;
  }
  &__img {
    display: inline-block;
    @include bg-img-ratio('../../assets/scan', 616, 553);
  }
}
</style>
